body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Box {
    width: 100%;
    /* padding: 10%; */
    border: 2px solid #0AB168;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, .1), 0 2px 4px 1px rgba(0, 0, 0, .10);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.73)
}

.Box input {
    width: 100%;
    border: none;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    color: rgba(0, 0, 0, 0.73);
    padding: 7px 5px;
    box-sizing: border-box;
    outline: none;
}

.Box ul {
    list-style-type: none;
    text-align: left;
    margin: 0;
    padding: 0;
    /*border-top: 1px solid lightskyblue;*/
}

.Box ul::before {
    content: "";
}

.Box li {
    padding: 10px 5px;
    cursor: pointer;
    border-top: 1px solid white;
    /*background-color: rgba(128, 128, 128, 0.20);*/
    background-color: rgb(148, 151, 144);
}

.Box li:hover {
    /*background-color: rgb(99, 151, 105);*/
    background-color: rgb(8, 153, 91);
    color: white;
}

