.App-Container {
    display: flex;
    justify-content: center;
    margin: 3rem auto 4rem auto;
}

.App-Container h2 {
    margin: 24px 90px auto 90px;
    color: rgb(9, 191, 114);
}

.App-Component {
    margin-top: 10px;
    width: 250px;
}

@media (max-width: 768px) {
    .App {
        align-content: center;
        justify-content: space-around;
    }

    .App h2 {
        /*margin: 2px 0 2px 0;*/
        margin: 0;
        padding: 0;
        font-size: calc(6px + 3vmin);
    }

    .App h6 {
        /*margin: 0 auto 0 auto;*/
        margin: 0;
        font-size: calc(4px + 3vmin);
        background-color: rgb(10, 177, 104);
    }

    .App p {
        line-height: 12px;
        font-size: calc(3px + 2vmin);
        margin: 0 auto 0 auto;
        padding: 0;
    }

    .App-Container {
        display: block;
        margin: 0 auto 0 auto;
        /*margin-top: 0;*/
    }

    .App-Component {
        display: inline;
    }

    .Items-Data {
        font-size: calc(6px + 3vmin);
        margin-bottom: 0;
    }
}


